*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html{
  font-size: 22px;
}
body{
  background-color:rgb(255, 239, 239);
  background-color: #287094;
  margin: 0 20%;
  width: 100wh;

}
#root{
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App{
  height: 100vh;
  width: 100%;
  background-color: #154d6a;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items:center;
  border-radius: 10% 40%;
  box-shadow: 5px 5px 100px black;
}

header{
  width: 100%;
  padding: 0 0.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-shadow: 5px 5px 10px #fff;
  color: rgb(251, 99, 124);
}
nav label{
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
}
nav ul li{
  font-size: 20px;
  display: inline-block;
  padding: 0 0.5rem;
  margin-top: 20px;
}
nav ul li a{
  color:white;
  padding: 5px 2px;
  text-decoration: none;
  border-radius: 10px;
  cursor: pointer;
  transition:background-color 2s ;
}
nav ul li a:hover{
  background-color: #ffff;
  color:black;
}
main{
  width: 50%;
  margin-top: 20px;
  background-color:  #023246;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: center;
  align-items: center;
  overflow-y: auto;
  box-shadow: 2px 2px 12px white;
  border-radius: 10%;

}
.addform{
  font-size: 30px;
  padding:0.5rem;
  margin-left: 2rem;
}
.addform label{
  position: absolute;
  left: -99999px;
}
.addform button{
  width: 35px;
  height: 35px;
  background-color:lightblue;
  border: 1px solid black;
  color:black;
  font-size: 15px;
  border-radius: 20px;
  cursor: pointer;
}
.addform button svg{
    margin-top: 3px;
}
.addform button:hover{
  background-color: lightgreen;
  color: white;
}
.addform input[type="text"]{
  flex-grow: 1;
  max-width: calc(100%-50px);
  padding: 0.25rem;
  margin-right: 0.25rem; 
  font-size: 1rem; 
  outline: none;
  border-radius: 20px;
  max-height: 48px;
}
.searchitem{
  margin-left: 2.5rem;
}
.searchitem label{
  position: absolute;
  left: -99999px;
}
.searchitem input[type="text"]{
  flex-grow: 1;
  max-width: calc(100%-50px);
  padding: 0.25rem;
  margin-right: 2rem; 
  font-size: 1rem; 
  outline: none;
  border-radius: 20px;
  max-height: 48px;
}


footer{
  width: 100%;
  padding: 0.25rem;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

ul{
  width: 100%;
  list-style: none;
  padding: 0 0.25rem 0.25rem;
}
ul li::before{
  content: "\200B";
}

.item{
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0.25rem 0 0.25rem 0.25rem;
  margin: 0.25rem 0;
  margin-left: 40px;
  background-color: #eee;
  border-radius: 10px;
  transition: 2s;
}
.item:hover{
  transform: scale(1.1);
}
.item :first-child{
  margin: 0;
}
.item input[type="checkbox"]{
  text-align: center;
  width: 2.5rem;
  width: 28px;
  min-width: 28px;
  height: 2.5rem;
  height: 30px;
  min-height: 30px;
  text-decoration: dashed;
  cursor: pointer;
  margin-right: 0.5rem;

}
.item input[type="checkbox"]:focus+label{
  text-decoration: underline;

}
.item label{
  font-size: 25px;
  flex-grow: 1;
}
.item svg{
  width: 48px;
  min-width: 48px;
  height: 20px;
  color: steelblue;
  cursor: pointer;
  outline: none;
  transition: 1s;
}
.item svg:hover{
  transform: scale(0.9);
  color: red;
  height: 30px;

}
.storedate{
  width: 100%;
  display: flex;
  flex-direction:column;
  flex-grow: 1;
  justify-content: flex-start;
  align-items: center;
  text-align: center;

}

.storedate button{
  padding: 10px;
  border-radius:5px;
  border: 1px solid black;
  outline: none;
}
.storedate button:hover{
  background-color: aquamarine;
}
.dates{
  width: 100%;
  font-size: 30px;
  flex-grow: 1;
}
.dates1{
  width: 500px;
  font-size: 30px;
  flex-grow: 1;
}
.storedate input[type="text"]{
  width: 100px;
  margin-right: 0.5rem;
}
.Listed_dates{
  display: grid;
  grid-template-columns: repeat(2,1fr);
}

@media only screen and (max-width: 600px){
  body{
    background-color:rgb(255, 239, 239);
    background-color: #287094;
    margin: 0 5%;
    width: 100wh;
  }
  .App{
    height: 100vh;
    width: 100%;
    background-color: #154d6a;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10% 20%;
    box-shadow: 5px 5px 100px black;
  }
 
  .addform input[type="text"]{
    flex-grow: 1;
     width:150px;
    padding: 0.25rem;
    margin-right: 0.25rem; 
    font-size: 0.8rem; 
    outline: none;
    border-radius: 20px;
    max-height: 48px;
  }
  .searchitem input[type="text"]{
    flex-grow: 1;
    width: 150px;
    padding: 0.25rem;
    margin-right: 2rem; 
    font-size: 0.8rem; 
    outline: none;
    border-radius: 20px;
    
  }
